@import url(partials/reset.css);

@import url(partials/fonts.css);

/* my style */

a {
  text-decoration: none;
}

*:focus {
  outline: none;
}

html {
  width: 100%;
}

body {
  font-family: "Roboto", sans-serif;
  margin: 0;
  font-size: 1vw;
  width: 100%;
  overflow-x: hidden;
  background: #2A2A2A;
  color: #FFFFFF;
}

.container {
  width: 60.625vw;
  margin-left: 26.35417vw;
  padding-top: 6.09375vw;
  position: relative;
  z-index: 20;
}

.wrapper {
  overflow: hidden;
  width: 100%;
  position: relative;
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;
}

.header__menu {
  list-style: none;
  padding: 0.26042vw 0 0;
  margin: 0 0 0 20vw;
  display: flex;
  align-items: center;
}

.header__menu li + li {
  margin-left: 1.40625vw;
}

.header__menu a {
  font-weight: 300;
  font-size: 1.66667vw;
  color: #fff;
  position: relative;
  display: flex;
  align-items: center;
}

.header__menu a::before {
  content: "";
  display: block;
  width: 0.88542vw;
  height: 3.28125vw;
  background: #872C1C;
  box-sizing: border-box;
  margin-right: 0.26042vw;
}

.header__menu a.active::before {
  background: #ed4f34;
  border: 1px solid #fff;
}

.static {
  position: fixed;
  display: block;
}

.static_1 {
  left: 13.75vw;
  top: 4.01042vw;
  z-index: 3;
  width: 76.92708vw;
}

.static_2 {
  width: 12.65625vw;
  left: 6.04167vw;
  top: 6.09375vw;
  z-index: 1;
}

.static_3 {
  width: 36.5625vw;
  left: 24.58333vw;
  top: 1.71875vw;
  z-index: 2;
}

.static_4 {
  width: 50.72917vw;
  left: 73.90625vw;
  top: -19.27083vw;
  z-index: 4;
}

.home-page .static_2 {
  top: 8.75vw;
}

.content-page .static_1 {
  opacity: 0.63;
}

.content-page .static_2 {
  opacity: 0.7;
}

.content-page_1 .container {
  width: 59.6875vw;
  margin-left: 26.92708vw;
}

.first-screen {
  position: relative;
  z-index: 10;
}

.first-screen__date {
  font-family: "News Cycle", "Roboto", sans-serif;
  font-weight: normal;
  font-size: 3.80208vw;
  text-align: left;
  color: #e5e5e5;
  position: relative;
  padding-left: 2.76042vw;
  padding-top: 4.58333vw;
  margin-left: 3.33333vw;
}

.first-screen__date::after {
  content: "";
  display: block;
  width: 47.39583vw;
  height: 0.57292vw;
  background: #555;
  opacity: 0.9;
  margin-top: -0.36458vw;
  position: relative;
  left: -2.76042vw;
  z-index: -1;
}

.content-page_1 {
  margin-top: -4.84375vw;
}

.movie-block {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
}

.movie-block + .movie-block {
  margin-top: 7.5vw;
}

.movie-block__left {
  width: 10.72917vw;
  padding-top: 4.84375vw;
}

.movie-block__btn {
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 300;
  font-size: 1.30208vw;
  text-align: left;
  color: #fff;
  width: 100%;
  height: 4.42708vw;
  background: rgba(237, 79, 52, 0.7);
  border: 4px solid rgba(255, 255, 255, 0.53);
  box-sizing: border-box;
}

.movie-block__right {
  width: 47.96875vw;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.movie-block__date {
  height: 4.84375vw;
  width: 100%;
  background: #4d4d4d;
  border: 1px solid #707070;
  opacity: 0.74;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  padding: 0 1.40625vw;
  font-weight: 300;
  font-size: 1.97917vw;
  text-align: right;
  color: #fff;
}

.movie-block__img {
  height: 26.92708vw;
  width: 100%;
  position: relative;
}

.movie-block__img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.movie-block__img::before {
  content: "";
  display: block;
  position: absolute;
  width: 1.09375vw;
  height: 100%;
  background: #b24330;
  left: 0;
  top: 0;
}

.movie-block__img::after {
  content: "";
  display: block;
  position: absolute;
  background: url("../img/video_after.svg") center center no-repeat;
  right: 1.40625vw;
  top: -0.72917vw;
  width: 2.08333vw;
  height: 25.15625vw;
}

.movie-block__title {
  font-weight: 300;
  font-size: 3.38542vw;
  text-align: left;
  color: #fff;
  background: rgba(237, 79, 52, 0.71);
  padding: 0 0.67708vw;
  margin-top: 0.41667vw;
}

.movie-block__info {
  margin-top: 0.26042vw;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}

.movie-block__staff {
  width: 11.82292vw;
  padding: 0.78125vw 0.625vw 5.20833vw 0.78125vw;
  box-sizing: border-box;
  background: url("../img/video_staff.svg") center top no-repeat;
  background-size: 100% 100%;
  flex-shrink: 0;
}

.movie-block__staff h3 {
  font-weight: 300;
  font-size: 0.9375vw;
  text-align: left;
  color: #000;
  margin: 0;
}

.movie-block__staff ul {
  list-style: none;
  font-weight: normal;
  font-size: 0.9375vw;
  text-align: left;
  color: #000;
}

.movie-block__staff ul + h3 {
  margin-top: 1.25vw;
}